@import "./Model.scss";

/* ---------- reset css start  ---------- */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
html {
  scroll-behavior: smooth;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
/* ---------- reset css end  ----------  */

p {
  margin: 12px 0;
  line-height: 24px;
}

#root {
  background-color: #ececec;
  overflow: hidden;
}

footer {
  text-align: center;
  padding: 8px;
  font-size: 14px;
}

.container {
  overflow: hidden;
  margin: 24px;
  background-color: $siteColor;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  border-radius: 8px;
}

h1 {
  font-size: 32px;
}

.main-column {
  width: 78%;
  float: right;
  background: #fff;
  min-height: 750px;
}

.main-column h2 {
  font-size: 28px;
  border-left: 4px solid $siteColor;
  padding-left: 8px;
}

.black-btn {
  color: $white !important;
  background-color: $black !important;
  text-transform: none !important;
}

.summary-description {
  width: 60%;
  float: left;
}

.summary-image {
  width: 40%;
  float: left;
}

.summary-image {
  text-align: center;
}

.main-thumnail {
  max-width: 75%;
}

.icon li {
  display: inline-block;
}

.icon {
  display: inline-block;
  margin-bottom: 8px;
}

.app-icon {
  width: 36px;
  height: 36px;
  margin-right: 8px;
}

.block {
  padding: 4% 6%;
  overflow: hidden;
}

.center {
  text-align: center;
}

.font-little-bold {
  font-weight: 600;
}
